import React from 'react'
import { Card } from 'react-bootstrap';
import styled from 'styled-components';
import RouteButtonGroup from './RouteButtonGroup';
import { RouteButtonGroupType } from './RouteButtonGroupType';

interface RouteTitleProps {
	icon: React.ReactNode
	title: string
	subTitle: string | null

	currentKey: string | null | undefined
	buttons: RouteButtonGroupType[]
}

const RouteTitle: React.FC<RouteTitleProps> = (
	{
		icon,
		title,
		subTitle = null,
		currentKey = null,
		buttons = [],
	}
) => {
	return (
		<Container>
			<CardContainer>
				<Card.Body>
					<TitleContainer>
						<Icon>{icon}</Icon>
						<Title>{title}</Title>
						<Subtitle>{subTitle}</Subtitle>
					</TitleContainer>


					{
						buttons.length !== 0 && 
						<>
							<div className="mb-3"></div>
							<RouteButtonGroup buttons={buttons} currentKey={currentKey} />
						</>
					}
				</Card.Body>
			</CardContainer>
		</Container>
	)
}

const Container = styled.div.attrs({
	className: 'd-flex flex-wrap',
})`
	margin-top: 1rem;
	margin-bottom: 1.2rem;
`

const CardContainer = styled(Card)`
	width: 100%;
`;

const TitleContainer = styled.div`
	display: flex;
`;

const Icon = styled.h1.attrs({
	className: `text-book ms-3 me-3 pt-1`,
})``

const Title = styled.h1.attrs({
	className: 'pt-2',
})``

const Subtitle = styled.h6.attrs({
	className: 'text-secondary ms-4 ms-md-2 mt-2 mt-md-3 mb-0 mb-md-1 clamp-1-line pt-md-3 pt-3',
})``

export default RouteTitle